.footer{
    height: 60px;
    width: 100%;
    background: linear-gradient(to right, #667db6, #0082c8, #0082c8, #667db6);
    flex-direction: row;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.generalInfo{
    display: flex;
    align-items: center;
    column-gap: 20px;
    justify-content: center;
    margin-left: 20px;
}
.generalInfo .p{
    text-transform: lowercase;
    color: white;
    font-size: small;
    padding-top: 4px;

    
}
.generalInfo i{
    color: white;
    font-size: small;
}
.time-working{
    font-size: small;
    color: white;
}
.info i{
    color: white;
}
