@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;400&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,400;1,200;1,300&display=swap');
.header{
    width: 100%;
    margin: 0px auto;
    height: 120vh;
    font-family: 'Poppins', sans-serif;
}
.blur{
    position: absolute;
    border-radius: 50%;
    filter: blur(220px);
}
.blur1{
    
    top: 0;
    right: 0;
    width: 400px;
    height: 400px;
    z-index: -4;
    background-color: rgb(7, 167, 230);
}
.blur2{
   
    bottom: 0;
    left: 0;
    width: 300px;
    height: 300px;
    z-index: -2;
    border-radius: 50%;
    background-color: rgb(248, 10, 10);
}
.navbar{
    position: relative;
}
.nav-brand{
    background-image: url('../../img/alpha3.1.png');
    height: 50px;
    width: 50px;
    background-size: cover;
}
.HandleBtn{
    position: absolute;
    bottom: 20px;
    color: rgb(55, 146, 231);
    left: 10px;
    font-size: 24px;


}
.nav-link{
color: rgb(3, 89, 160);
}
.nav-link:hover{
    background-color: rgb(75, 173, 238);
    color: white;
    border-radius: 4px;

}
.Box{
    transform: translateY(0px);
}
.Box.opened{
    transform: translateY(80px);
    z-index: 1;
}
.main-photo{
    background-image: url('../../img/AlphaLogo.png');
    background-size: cover;
    width: 40%;
    height: 80%;
    filter: drop-shadow(2px 2px 5px rgb(6, 112, 173));
}

.main-content h1{
    font-weight: 400;
}
.main-content h1 span{

font-weight: 100;

}
.main-content h6{
    height: 70px;
    padding-block: 20px;
}

.main-content h4{
    padding-block: 20px;
}
.join{
    padding: 10px 20px;
    border-radius: 5px;
    color: white;
    text-decoration: none;
    border: 1px solid wrgb(3, 89, 160);
    background-color:rgb(10, 125, 219) ;
}
.join:hover{
    background-color: white;
    color: rgb(16, 128, 219);

    border: 1px solid white;
}
.alphaTitle{
    text-align: center;
    font-weight: 100;
    letter-spacing: 6px;
    padding-block: 40px;
    color: rgb(3, 89, 160);
    position: relative;
}

.alphaTitle span{
 color: black;
    font-weight: 400;
}