@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;400&display=swap');
.Event{
    height: 50vh;
    width: 100%;
    font-family: 'Poppins', sans-serif;
  background-size: cover;
}
.Event h1{ 
font-size: 60px;
font-weight: 400;
padding-block: 30px;
text-align: center;
}
.Event-png{
    height: 400px;
    background-image: url('../../img/undraw_Online_calendar_re_wk3t (1).png');
    width: 600px;
    background-size: cover;
}
.Event-content{
    width: 600px;
    padding: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.content{
    width: 90%;
display: flex;
flex-direction: column;
align-items: center;
}
.content h3{
    letter-spacing: 4px;
    position: relative;
}


.likeBtn{
    border: none;
    margin-top: 20px;
}
.lignTitle{
    height: 3px;
    width: 140px;
    background-color: rgb(14, 160, 218);
}