@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;400&display=swap');
.partner{
    height: 60vh;
    width: 80%;
    margin: 60vh auto;

}
.partner-photo{
    background-image: url('../../img/huwawei.JPG');
background-size: cover;
height: 300px;
z-index: 4;
border-radius: 40px;
width: 50%;
margin: 30px auto;
transition: .4s ease-in-out;
}

.partner h3{
    text-align: center;
    padding-block: 40px;
    font-family: 'Poppins' ,'sans-serif';
}
.partner h3 span{
    text-transform: capitalize;
    color: rgb(28, 95, 240);
    
}
