@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;400&display=swap');
.contact{
    height: 120px;
    width: 80%;
    margin: 50px auto;
    background: white;
}
.contact h3{
    text-align: center;
    font-family: 'Poppins' ,'sans-serif';
    padding-block: 20px;
}
.icons-social{
    display: flex;
    width: 50%;
    justify-content: center;
    margin: 20px auto;
    gap: 20px;
}
.contact .icons-social i{
    font-size: 30px;

}
